<template>
  <div>
    <pc-index v-if="device=='pc'"></pc-index>
    <phone-index v-else></phone-index>
  </div>
</template>

<script>
  import PcIndex from './PcIndex.vue'
  import PhoneIndex from './PhoneIndex.vue'
  export default {
    name: 'Index',
    components: {
      PcIndex,PhoneIndex
    },
    data () {
      return {
        state: 'N',
        device:'pc',
        pager: {
          currPage: 0,
          total: 0,
          pageSize: 5,
          sorts: [{col: 'created', op: 'desc'}],
        },
        surveyList: []
      }
    },
    created(){
       this.device = this.Fun.getDevice()
    },

    mounted(){
      this.userLogin();
      this.querySurveyList();
    },
    methods: {
      userLogin() {
        let _reqObj = {
          url: 'pub/userLogin',
        }
        return this.SERVICE2(_reqObj).then(res => {
          this.state = res.data.res;
        })
      },
      design(){
        location.href = 'design.html'
      },
      login(){
        location.href = 'login.html'
      },
      register(){
        location.href = 'login.html#/Register'
      },
      pageQuerySurvey(currPage){
        this.pager.currPage = currPage;
        this.querySurveyList();
      },
      clickQuerySurvey(){
        this.pager.currPage = 0;
        this.pager.total = 0;
        this.querySurveyList();
      },
      querySurveyList() {
        let _reqObj = {
          url: 'pub/pubSurveyList',
          data: this.pager,
          method: 'post',
        }
        return this.SERVICE2(_reqObj).then(res => {
          //console.log(JSON.stringify(res.data))
          this.pager.total = res.data.res.total;
          this.surveyList = res.data.res.rows;
        })
      },

    }
  }
</script>

<style type="less">

  .thumbnail {
    border: none;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }



</style>



