  import {BaseQuesClass} from './BaseClass'
  import Fun from '../Fun'
  let inc = 0 ;
  /**
   * 填空题
   */
  class InputClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'Input';
      this.htmlTitle = '<p><strong>填空题</strong></p>';
      this.leftTitle = '';//左边标题
      this.rightTitle = '';//右边标题
      this.varName = 'C_'+Fun.getInc();
      this.fieldName =  this.varName;
      this.rule = '';
      this.width = '' ;
      this.pcWidth = '350px' ;
      this.phoneWidth = '100%' ;
      this.row = '1';
    }
    /**
     * 验证答题
     * @param an
     * @returns {boolean}
     */
    anValid(an){
      if (this.required) {
        if (Fun.isNull(an[this.varName])) {
          this.errInfo = "这道题必须回答哦！"
          return false
        }else{
          if(!Fun.isNull(this.rule)){//有规则
            let tip = Fun.validRule(this.rule,an[this.varName])
            if(!Fun.isNull(tip)){
              this.errInfo = tip
              return false
            }
          }
        }
      }
      this.errInfo = ""
      return true
    }
    getVarNames(){
      let varSet = new Set();
      varSet.add(this.varName)
      return varSet
    }
    /**
     * 答题界面样式配置
     */
    anInit(){
      super.anInit();
      if(this.device=='pc'){
        this.width = this.pcWidth ;
      }else{
        this.width = this.phoneWidth ;
      }
    }
  }
  export default InputClass




