import Fun from "./Fun";
class ElNode {
  constructor(jsonObj) {
    this.type = jsonObj.type;
    this.quesId = jsonObj.quesId;
    this.varName = jsonObj.varName;
    this.op = jsonObj.op;
    this.val = jsonObj.val;
    this.desc = jsonObj.desc;
    this.childs = undefined;
    this.dep = 1;
    this.maxDep = 1;
  }

  /**
   * 节点是否是逻辑关系符
   */
  isLogic() {
    if (this.type == '||' || this.type == '&&') {
      return true
    }
    return false
  }

  hasEl() {
    if (this.quesId !== undefined && this.quesId != '') {
      return true
    }
    return false
  }

  /**
   * 表达式是否输入完整
   * @returns {boolean}
   */
  elFinish() {
    if (!this.isLogic()) {
      if (Fun.isNull(this.quesId) || Fun.isNull(this.varName) || Fun.isNull(this.op) || Fun.isNull(this.val)) {
        return false
      }
    }
    return true
  }

  /**
   * 当前节点下插入一个节点，返回新插入的节点对象
   */
  insertNode(json) {
    if (this.childs === undefined) {
      this.childs = [];
    }
    let node = new ElNode(json)
    this.childs.push(node)
    return node
  }

  /**
   * 当前节点占几行，即计算多少个叶节点
   */
  getRows() {
    if (this.childs === undefined || this.childs.length == 0) {
      return 1;
    } else {
      let _rows = 0;
      for (let _node of this.childs) {
        _rows += _node.getRows()
      }
      return _rows
    }
  }

  getCols() {
    if (this.isLogic()) {//逻辑点，占一列
      return 1
    } else {
      return this.maxDep - this.dep + 1
    }
  }
}
class ElTree {
  constructor() {
    this.root = undefined;
    this.maxDep = 0;//树的最大深度
    this.flag = false;
    this.strEl = null;
    this.defaultInit()
  }

  defaultInit() {
    this.root = new ElNode({type: '&&'})
    this.root.insertNode({type: 'el'})
  }

  /**
   * 表达式的逻辑值 true false
   * @param an
   * @returns {*}
   */
  elLogicVal(an,varSet) {
    try {
      this.evalEl(varSet);
      if (this.strEl == "") {
        return true;//满足要求
      }
      console.log("strEl="+this.strEl)
      let func = eval.call(null, this.strEl);
      let res = func(an);
      console.log("res="+res)
      if(res=='true'){
        return true
      }else{
        return false
      }
    } catch (err) {
      console.log(err.message)
      return true
    }
  }

  evalEl(varSet) {
    this.strEl = ""
    //console.log(JSON.stringify(this.root))
    if (!this.validElFinish()) {
      return;//异常处理，不完整作为没有填写表达式
    }
    this.strEl = '(an) => `${';
    this.inOrderEl(this.root,varSet)
    this.strEl += '}`'
  }


  /**
   * 中序递归表达式
   * @param node
   * @param varSet Set 结构所有可用的变量名，反复修改问题，导致变量名成为垃圾了
   */
  inOrderEl(node,varSet) {
    this.strEl += '('
    let index = 0;
    for (let childNode of node.childs) {
      if (index > 0) {
        this.strEl += node.type
      }
      if (!Fun.isNull(childNode.childs)) {
        this.inOrderEl(childNode,varSet);
      } else {
        if(varSet.has(childNode.varName)){
          this.strEl += '('
          // 数值类型不用加引号
          let val = window.isNaN(childNode.val) ? '"' + childNode.val + '"' : childNode.val
          this.strEl += 'an["' + childNode.varName + '"]' + childNode.op + val
          this.strEl += ')'
        }else{
          this.strEl += true ;//变量已不存在，默认为真
        }

      }
      index++;
    }
    this.strEl += ')'
  }

  init() {
    if (this.root !== undefined) {
      this.maxDep = this.calcTreeDep(this.root)
      this.calcLeafDep(this.root, 1)
    }
  }

  delEl(leafNode) {
    this.flag = false;
    this.delNode(leafNode, this.root)
  }

  /**
   * 删除节点
   */
  delNode(leafNode, preNode) {
    if (preNode.childs === undefined) {
      return
    }
    if (this.flag) {
      return
    }
    for (let i = 0; i < preNode.childs.length; i++) {
      if (preNode.childs[i] === leafNode) {
        preNode.childs.splice(i, 1)
        if (preNode.childs.length == 0) {//如果没有子节点，再次向上删除
          this.delNode(preNode, this.root)
        } else {
          this.flag = true;
          return
        }
      } else {
        this.delNode(leafNode, preNode.childs[i]);//继续往下找
      }
    }
  }

  /**
   * 验证表达式输入是否完整
   * @param currNode
   * @param flag
   */
  validElFinish() {
    if (this.root.maxDep <= 1) {//只有逻辑符
      return false
    }
    this.flag = true;
    this.recursiveValidElFinish(this.root)
    return this.flag;
  }

  /**
   * 验证表达式输入是否完整
   * @param currNode
   * @param flag
   */
  recursiveValidElFinish(currNode) {
    if (!currNode.elFinish()) {
      this.flag = false;
      return;
    } else {
      if (currNode.childs !== undefined) {
        for (let childNode of currNode.childs) {
          this.recursiveValidElFinish(childNode)
        }
      }
    }
  }

  /**
   * 已有过滤条件转成树
   */
  buildByFilter(filter) {
    this.root = new ElNode(filter)
    this.filter2Tree(filter, this.root)
  }

  filter2Tree(filter, currNode) {
    if (filter.childs !== undefined) {
      for (let childsFilter of filter.childs) {
        let childsNode = currNode.insertNode(childsFilter)
        this.filter2Tree(childsFilter, childsNode)
      }
    }
  }

  insertLogic(preNode) {
    let nextNode = preNode.insertNode({type: '&&'})
    nextNode.insertNode({type: 'el'})
  }

  insertEl(preNode) {
    preNode.insertNode({type: 'el'})
  }


  /**
   * 计算节点深度
   */
  calcLeafDep(currNode, currDep) {
    currNode.dep = currDep;
    currNode.maxDep = this.maxDep;//每个节点都有树的最大深度
    if (currNode.childs !== undefined) {
      for (let childsNode of currNode.childs) {
        this.calcLeafDep(childsNode, currDep + 1)
      }
    }
  }

  /**
   * 计算树的最大深度
   */
  calcTreeDep(currNode) {
    if (currNode.childs === undefined) {
      return 1;
    }
    let max = 0;
    for (let childsNode of currNode.childs) {
      let height = this.calcTreeDep(childsNode)
      if (height > max) {
        max = height;
      }
    }
    return max + 1
  }

  /**
   * 查询条件表达式转html表格需要的数组方式，数组里的每个节点是个完整的子树
   * @param treeRoot
   * @returns {any[]|Array}
   */
  tree2Arr() {
    this.init()
    if (this.root === undefined) {
      return new Array()
    }
    let stack = new Array()
    stack.push(this.root)
    let arr = new Array()
    let childsArr = new Array();//表格每一行所有单元格保存在一个数组
    arr.push(childsArr)
    this.tree2Tr(arr, childsArr, stack);//递归处理每一行
    return arr;
  }

  /**
   * 转化成表格的每一行存放一个数组里
   * @param arr
   * @param childsArr
   * @param stack
   */
  tree2Tr(arr, childsArr, stack) {
    if (stack.length > 0) {
      let node = stack.pop();//从堆栈弹出树
      childsArr.push(node);//插入树的一个节点
      if (node.childs !== undefined && node.childs.length > 0) {
        for (let i = node.childs.length - 1; i >= 0; i--) {
          stack.push(node.childs[i]);//当前待处理节点加入堆栈
        }
        this.tree2Tr(arr, childsArr, stack);//递归调用下面的节点放在数组里
      }
      if (stack.length > 0) {
        let childsArray = new Array();//产生新的一行
        arr.push(childsArray)
        this.tree2Tr(arr, childsArray, stack)
      }
    }
  }
}

export default {
  ElTree
}



