import axios from "axios";
import {Message} from "element-ui";
//require('es6-promise').polyfill();
const service = axios.create({
  timeout: 60000, // 请求超时时间
  baseURL: BASE_URL, // api的base_url
  withCredentials: true
})
//axios.defaults.headers.post['Content-Type'] = 'application/json'
//axios.defaults.baseURL = BASE_URL

// Add a request interceptor
service.interceptors.request.use(function (config) {
  // Do something before request is sent
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});

// http response 拦截器
service.interceptors.response.use(
  response => {
    let _res = response.data
    if (_res.status === 'O') {//session过期
      parent.location.href = "login.html";
      return Promise.reject(_res);
    } else if (_res.status === 'F') {
      Message({
        message: _res.msg,
        duration: 5000,
        type: 'error',
        showClose: true,
      });
      return Promise.reject(response); // 返回接口返回的错误信息
    } else if (_res.status === 'T'&&_res.code=='01') {//问卷没发布,code有值都表示没有数据
      return Promise.reject(response); // 没有问卷数据，同时抛出异常
    }
    return response;
  },
  error => {
    Message({
      message: error,
      duration: 10000,
      type: 'error',
      showClose: true
    });
    return Promise.reject(error); // 返回接口返回的错误信息
  });

export default service



