import Vue from "vue";
import Router from "vue-router";
import Index from "@/components/index/Index";

Vue.use(Router)

const router = new Router({
  routes: [
    {path: '/', redirect: '/Index'},
    {
      path: '/Index',
      name: 'Index',
      components: {default: Index},
    }
  ],
})

router.beforeEach((to, from, next) => {
  if (to.matched.length === 0) {
    return next("/Index")
  } else {
    next(); //如果匹配到正确跳转
  }
});
export default router;



