import Fun from "./Fun.js";
import SERVICE2 from "../config/service2.js";

let WXjssdk = {//声明微信全局变量，防止污染外部环境
  version: "1.0",//版本号
  appName: "kukaSurvey", //使用当前库的开发者，可以配置应用名字
  isReady: false,//微信JS SDK是否初始化完毕
  access_token: "",//令牌
  ticket: "",//微信临时票据
  config: {
    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
    appId: 'wxf63021da44864cf1', // 必填，公众号的唯一标识
    timestamp: Math.ceil(new Date().getTime() / 1000).toString(), // 必填，生成签名的时间戳
    nonceStr: 'html5kuka_wxJSSDK', // 必填，生成签名的随机串
    signature: '',// 必填，签名，见附录1
    jsApiList: [
      "onMenuShareAppMessage",
      "onMenuShareTimeline",
      'hideMenuItems',
      "getLocation",
      "openLocation"
    ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
  },
  /*
   函数功能：初始化
   */
  init: function () {
    if(!Fun.isWeiXin()){//是否是微信浏览器
      return;
    }
    if (!wx) {//验证是否存在微信的js组件
      alert("微信接口调用失败，请检查是否引入微信js！");
      return;
    }
    let that = this;//保存当前作用域，方便回调函数使用
    //获取令牌
    this.wx_get_token(function (data) {
      //alert("a1="+data.access_token)
      if (data.access_token) {
        Cookie.Set("access_token", data.access_token, 3600);
        that.access_token = data.access_token;
      }
      //获取票据
      that.wx_get_ticket(function (data) {
        if (data.ticket) {
          Cookie.Set("ticket", data.ticket, 3600);
          that.ticket = data.ticket
        }
        //获取签名
        that.wx_get_signature(function (data) {
          that.config.signature = data;
          that.initWx(function () {//初始化微信接口
            //初始化完成后的执行
          });
        });
      });
    });
  },
  //获取令牌
  wx_get_token: function (call) {
    this.access_token = Cookie.Get("access_token");
    if (!Cookie.Get("access_token")) {
      SERVICE2({url: 'wx/getAccessToken'}).then(res => {
        call && call(res.data.res);
      })
    } else {
      call && call({});
    }
  },
  //获取票据
  wx_get_ticket: function (call) {
    this.ticket = Cookie.Get("ticket");
    if (!this.ticket) {
      let _reqObj = {
        url: 'wx/getJsapiTicket',
        params: {access_token: this.access_token,}
      }
      SERVICE2(_reqObj).then(res => {
        call && call(res.data.res);
      })
    } else {
      call && call({});
    }

  },
  //获取签名
  wx_get_signature: function (call) {
    let _reqObj = {
      url: 'wx/getSignature',
      params: {
        jsapi_ticket: this.ticket, // 必填，生成签名的时间戳
        timestamp: this.config.timestamp, // 必填，生成签名的时间戳
        noncestr: this.config.nonceStr, // 必填，生成签名的随机串
        url: window.location.href // 必填，生成签名的随机串
      }
    }
    SERVICE2(_reqObj).then(res => {
      //alert("signature="+res.data.res.signature)
      call && call(res.data.res.signature);
    })

  },
  initWx: function (call, errorCall) {//初始化微信接口
    let that = this;
    wx.config(this.config);//初始化配置
    /*config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，
     *config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，
     *则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，
     *则可以直接调用，不需要放在ready函数中。
     * */

    wx.ready(function () {
      // 在这里调用 API
        let title = "小白问卷-免费、简约、易用的在线调研",
        link = "http://www.kukababy.com",
        imgUrl = "http://www.kukababy.com/resource/img/logo.png",
        desc = "小白问卷-免费、简约、易用的在线调研",
        success = function () {
          //alert("分享成功!");
        },
        cancel = function () {
          //alert("分享失败!");
        };
      // wx.hideMenuItems({
      //   menuList: ['menuItem:share:appMessage'] // 要隐藏的菜单项，只能隐藏“传播类”和“保护类”按钮，所有menu项见附录3
      // });
      wx.onMenuShareTimeline({//分享到朋友圈
        //title: title, // 分享标题
        //link: link, // 分享链接
        imgUrl: imgUrl, // 分享图标
        success: function () {
          success();
        },
        cancel: function () {
          cancel();
        }
      });
      wx.onMenuShareAppMessage({
        title: title, // 分享标题
        desc: SYSCFG.wx.wxDesc, // 分享描述
        //link: link, // 分享链接
        imgUrl: imgUrl, // 分享图标
        type: "link", // 分享类型,music、video或link，不填默认为link
        dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
        success: function () {
          success();
        },
        cancel: function () {
          cancel();
        }
      });
      let latitude, longitude, speed, accuracy;
      // wx.getLocation({
      //   success: function (res) {
      //     latitude = res.latitude; 		// 纬度，浮点数，范围为90 ~ -90
      //     longitude = res.longitude; 	// 经度，浮点数，范围为180 ~ -180。
      //     speed = res.speed; 		    // 速度，以米/每秒计
      //     accuracy = res.accuracy; 	    // 位置精度
      //     //$("#accuracy").html(accuracy);
      //     let info = latitude+" "+longitude+" "+speed+" "+accuracy
      //     alert(info)
      //   }
      // });

      //$("#openLocation").click(function () {	//地图查看
      //   wx.openLocation({
      //     latitude: latitude, 		// 纬度，浮点数，范围为90 ~ -90
      //     longitude: longitude, 	// 经度，浮点数，范围为180 ~ -180
      //     name: '测试', 		// 位置名
      //     address: '测试地址', 	// 地址详情说明
      //     scale: 1, 			// 地图缩放级别,整形值,范围从1~28。默认为最大
      //     infoUrl: 'http://www.baidu.com' // 在查看位置界面底部显示的超链接
      //   });
      //});
      that.isReady = true;
      call && call();
    });
    /*config信息验证失败会执行error函数，如签名过期导致验证失败，
     *具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，
     * 对于SPA可以在这里更新签名。
     * */
    wx.error(function (res) {
      that.isReady = "false";
      errorCall && errorCall();
    });

  }
};
//执行初始化
WXjssdk.init();


