    data () {
      return {
        state: 'N',
        loading:false,
        wxUa: false,
        pager: {
          currPage: 0,
          total: 0,
          pageSize: 5,
          sorts: [{col: 'created', op: 'desc'}],
        },
        surveyList: []
      }
    },
    created(){
      this.wxUa = this.Fun.isWeiXin();
    },
    mounted(){
      this.userLogin();
      this.querySurveyList();
    },
    methods: {
      userLogin() {
        let _reqObj = {
          url: 'pub/userLogin',
        }
        return this.SERVICE2(_reqObj).then(res => {
          this.state = res.data.res;
        })
      },
      design(){
        if(this.state=='Y'){
          location.href = 'design.html#/SurveyList'
        }else{
          location.href = 'login.html'
        }
      },
      wxLogin(){
        if(this.state=='Y'){
          location.href = 'design.html#/SurveyList'
        }else{
          this.loading=true
          window.location.href = 'wxLoginRedirect'
        }
      },
      login(){
        location.href = 'login.html'
      },
      register(){
        location.href = 'login.html#/Register'
      },
      pageQuerySurvey(currPage){
        this.pager.currPage = currPage;
        this.querySurveyList();
      },
      clickQuerySurvey(){
        this.pager.currPage = 0;
        this.pager.total = 0;
        this.querySurveyList();
      },
      querySurveyList() {
        let _reqObj = {
          url: 'pub/pubSurveyList',
          data: this.pager,
          method: 'post',
        }
        return this.SERVICE2(_reqObj).then(res => {
          //console.log(JSON.stringify(res.data))
          this.pager.total = res.data.res.total;
          this.surveyList = res.data.res.rows;
        })
      },

    }
  }
</script>

