import {BaseQuesClass,BaseOptionClass} from './BaseClass'
import Fun from '../Fun'
/**
 * 选项
 */
class EndOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.val = 'A';
    this.desc = '正常结束';
    this.dft = false;
    this.url = 'http://www.kukababy.com/home.jsf'
    this.urlShow = false ;
  }
}
/**
 * 单选题
 */
class EndClass extends BaseQuesClass {
  constructor(design=true) {
    super();
    this.quesType = 'End';
    this.htmlTitle = '结束题';
    this.varName = Fun.getInc();
    this.fieldName = Fun.getInc();
    this.required = false;//必答题

    this.rows = [];
    if(design){//设计问题
      this.init();
    }
  }

  init(){
    let labels = ['正常结束']
    let rate = null
    for(let label of labels){
      rate = new EndOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }
  }
  getVarNames(){
    let varSet = new Set();
    varSet.add(this.varName)
    return varSet
  }
  addOption(index) {
    let option = new EndOptionClass();
    option.htmlLabel = "正常结束";
    option.val = 'A';
    if (index == -1) {//尾部插入
      this.rows.push(option)
    } else {//之后插入
      this.rows.splice(index + 1, 0, option);
    }
  }
  delOption(index){
    if (this.rows.length > 1) {
      this.rows.splice(index, 1)
    }
  }

}
export default EndClass




