import Fun from "../Fun.js";
import EL from "../EL.js";
/**
 * 基本选项
 */
class BaseOptionClass {
  constructor() {
    this.htmlLabel = '选项';
  }
}
/**
 * 基类题型
 */
class BaseQuesClass {
  constructor() {
    this._id='';
    this.quesId = '';
    this.surveyId = '';
    this.errInfo = '';//答题不通过的错误提示
    this.quesType = 'Input';
    this.htmlTitle = '填空题';
    this.descFlag = false;//描述控制显示
    this.htmlDesc = '';//问题描述，一般在标题下面
    this.tipsFlag = false;
    this.htmlTips = '';
    this.jumpEl = null;
    this.jumpPage = null;//跳转到哪页
    this.jumpEndVal = 'A'
    this.durationFlag = false;//答题时长秒
    this.duration = 0;
    this.required = true;//必答题
    this.requiredShow = true;//是否显示必答题的星号，用户可以在编辑器里加自己的提示

    this.device = 'phone';

    this.style = '';
    this.pcStyle = '';
    this.phoneStyle = '';

    this.paddingLeft = '';//明细项偏移
    this.pcPaddingLeft = '';
    this.phonePaddingLeft = '';

    //this.pcStyles = [{code: 'default', label: '默认'}];
    //this.mobileStyles = [{code: 'default', label: '默认'}];
    this.visibleEl = null;
  }

  anInit() {
    if (this.device == 'pc') {
      this.style = this.pcStyle;
      this.paddingLeft = this.pcPaddingLeft
    } else {
      this.style = this.phoneStyle;
      this.paddingLeft = this.phonePaddingLeft
    }
  }

  /**
   * 验证答题
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    return true
  }

  anRandom(){
  }

  getVarNames() {
    return []
  }

  /**
   * 是否配置了选项数量控制
   * @returns {boolean}
   */
  anHasOptionSum() {
    return false;
  }

  /**
   * 显示问题控制的所有引用变量
   * @returns {返回本题引用的所有变量}
   */
  getVisibleVarNames() {
    return this.getElVarNames('visibleEl')
  }

  /**
   * 当前问题表达式的所有变量
   */
  getElVarNames(elType) {
    let el = null;
    if (elType === 'visibleEl') {//显示表达式
      el = this.visibleEl;
    } else if (elType === 'jumpEl') {
      el = this.jumpEl;
    }
    //console.log(this.quesId)
    let varSet = new Set();
    if (Fun.isNull(el)) {//没有表达式
      return varSet;//没有表达式
    } else {
      this.callElVarNames(el, varSet);
    }
    return varSet;//返回本题引用的所有变量
  }

  /**
   * 递归每个子节点的变量
   * @param el
   * @param varSet
   */
  callElVarNames(el, varSet) {
    if (!Fun.isNull(el.type)) {
      if ((el.type === '||' || el.type === '&&') && !Fun.isNull(el.childs)) {
        for (let currEl of el.childs) {
          this.callElVarNames(currEl, varSet);
        }
      } else {//type=el
        if (!Fun.isNull(el.varName)) {
          varSet.add(el.varName);
        }
      }
    }
  }

  anDefaultVal(ue,an) {

  }

  getVisible(an, varNameSet) {
    let visible = true
    if (this.hasVisibleEl()) {
      let elTree = new EL.ElTree()
      elTree.buildByFilter(this.visibleEl)
      elTree.tree2Arr()
      visible = elTree.elLogicVal(an, varNameSet)
    }
    if (!visible) {
      this.removeNoVisibleAn(an)
    }
    return visible;
  }
  /**
   * 当前题不显示时，清空答案
   * @param an
   */
  removeNoVisibleAn(an){
    let varSet = this.getVarNames()
    for(let varName of varSet){
      if(!Fun.isNull(an[varName])){
        an[varName] = undefined ;
      }
    }
  }

  hasVisibleEl() {
    if (!Fun.isNull(this.visibleEl)) {
      return true
    } else {
      return false
    }
  }

  hasJumpEl() {
    if (!Fun.isNull(this.jumpEl)) {
      return true
    } else {
      return false
    }
  }
}
export{
  BaseOptionClass,
  BaseQuesClass,
}



